@import "../../style-config/variable.scss";
@import "../../style-config/mixins.scss";

.query_form {
  width: 100%;
  margin-left: 20px;

  @media only screen and (max-width: 1300px) {
    width: 100%;
    margin-top: 3rem;
    margin-left: 0;
  }

  form {
    display: flex;
    flex-flow: row wrap;
    margin-top: -10px;
    gap: 0; // Handled by margins on items
  }

  .input_field {
    width: calc(50% - 20px);
    margin: 10px;
    position: relative;

    &.full-width {
      width: calc(100% - 20px);
    }

    input,
    textarea {
      width: 100%;
      background: rgba(255, 255, 255, 0.05);
      border: 1px solid rgba(255, 255, 255, 0.1);
      border-radius: 8px;
      padding: 15px 20px;
      color: $white;
      outline: none;
      font-size: 16px;
      transition: all 0.3s ease;
      font-family: $base-font-family;

      &::placeholder {
        color: rgba(255, 255, 255, 0.3);
      }

      &:focus {
        border-color: $text-accent;
        box-shadow: 0 0 10px rgba(0, 242, 255, 0.1);
        background: rgba(255, 255, 255, 0.08);
      }
    }

    input {
      height: 55px;
    }

    textarea {
      height: 150px;
      resize: none;
    }

    .error {
      color: #ff4444;
      font-size: 12px;
      margin-top: 5px;
      padding-left: 5px;
    }
  }

  button {
    outline: 0;
    border: 0;
    padding: 12px 30px;
    background: linear-gradient(
      135deg,
      $text-accent 0%,
      $text-accent-secondary 100%
    );
    border-radius: 8px;
    color: $white;
    font-size: 18px;
    font-weight: 500;
    margin-left: 10px;
    margin-top: 10px;
    cursor: pointer;
    box-shadow: 0 4px 15px rgba(0, 242, 255, 0.2);
    transition: all 0.3s ease;

    &:hover {
      transform: translateY(-2px);
      box-shadow: 0 6px 20px rgba(112, 0, 255, 0.4);
    }

    @media only screen and (max-width: 1300px) {
      width: calc(100% - 20px);
    }
  }
}
