@import "../../style-config/variable.scss";
@import "../../style-config/mixins.scss";

.project_showcase {
  display: flex;
  flex-flow: row wrap;
  gap: 20px;
  justify-content: center;
  width: 100%;
  margin-top: 2rem;

  .showcase_item {
    width: 320px;
    height: 240px;
    overflow: hidden;
    border-radius: 12px;
    position: relative;
    cursor: pointer;
    border: 1px solid rgba(255, 255, 255, 0.1);
    background: $card-bg;

    @media only screen and (max-width: 768px) {
      width: 100%;
      height: 220px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: transform 0.5s ease;
    }

    .meta_content {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      padding: 20px;
      background: linear-gradient(to top, rgba(0, 0, 0, 0.9), transparent);
      opacity: 0;
      transform: translateY(20px);
      transition: all 0.3s ease;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;

      h3 {
        color: $white;
        font-size: 18px;
        font-weight: 500;
        margin-bottom: 5px;
        text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
      }

      .go_to_project .text {
        font-size: 14px;
        color: $text-accent;
      }

      @media only screen and (max-width: 768px) {
        opacity: 1;
        transform: translateY(0);
        background: linear-gradient(to top, rgba(0, 0, 0, 0.8), transparent);
      }
    }

    &:hover {
      img {
        transform: scale(1.1);
      }
      .meta_content {
        opacity: 1;
        transform: translateY(0);
      }
    }
  }
}
