@import "../../style-config/variable.scss";
@import "../../style-config/mixins.scss";

.intro_content {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  height: 100%;

  .layout {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    gap: 4rem;

    @media only screen and (max-width: 968px) {
      flex-direction: column-reverse;
      gap: 3rem;
      text-align: center;
    }

    .left_col {
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: center;

      @media only screen and (max-width: 968px) {
        align-items: center;
      }

      .title {
        display: flex;
        flex-direction: column;
        color: $white;
        margin-bottom: 1.5rem;

        .small_text {
          font-size: 24px;
          color: $text-secondary;
          margin-bottom: 0.5rem;
          font-weight: 500;
        }

        .big_text {
          font-size: 64px;
          font-weight: 700;
          line-height: 1.1;
          margin-bottom: 0.5rem;

          @media only screen and (max-width: 768px) {
            font-size: 42px;
          }
        }

        .sub_title {
          font-size: 28px;
          color: $text-secondary;
          font-weight: 400;

          @media only screen and (max-width: 768px) {
            font-size: 20px;
          }
        }
      }

      p {
        color: $text-secondary;
        font-size: 18px;
        line-height: 1.6;
        margin-bottom: 2rem;
        max-width: 500px;

        @media only screen and (max-width: 968px) {
          margin-left: auto;
          margin-right: auto;
        }
      }

      .btn-wrapper {
        display: flex;
        gap: 1rem;
      }
    }

    .right_col {
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;

      .image-container {
        position: relative;
        width: 350px;
        height: 350px;
        display: flex;
        justify-content: center;
        align-items: center;

        @media only screen and (max-width: 768px) {
          width: 280px;
          height: 280px;
        }

        img {
          width: 90%;
          height: 90%;
          object-fit: cover;
          border-radius: 50%;
          z-index: 2;
          border: 4px solid rgba(255, 255, 255, 0.1);
          box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
        }

        .circle-bg {
          position: absolute;
          width: 100%;
          height: 100%;
          border-radius: 50%;
          background: linear-gradient(
            135deg,
            rgba(0, 242, 255, 0.2) 0%,
            rgba(112, 0, 255, 0.2) 100%
          );
          z-index: 1;
          animation: pulse-glow 3s infinite ease-in-out;
        }
      }
    }
  }
}

@keyframes pulse-glow {
  0% {
    transform: scale(0.95);
    opacity: 0.5;
  }
  50% {
    transform: scale(1.05);
    opacity: 0.8;
  }
  100% {
    transform: scale(0.95);
    opacity: 0.5;
  }
}
