@import "../style-config/variable.scss";
@import "../style-config/mixins.scss";

.skills_container {
  display: flex;
  align-items: center;
  gap: 4rem;
  max-width: 1200px;
  margin: 0 auto;
  padding: 4rem 2rem;

  @media only screen and (max-width: 968px) {
    flex-direction: column;
    text-align: center;
    gap: 2rem;
  }

  .left_col {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;

    img {
      width: 100%;
      max-width: 500px;
      display: block;
      filter: drop-shadow(0 0 20px rgba(0, 242, 255, 0.2));
    }
  }

  .right_col {
    flex: 1.2;
    color: $white;

    h2 {
      margin-bottom: 2rem;
      font-size: 48px;
      font-weight: 700;
      @include small-screen {
        font-size: 36px;
      }
    }

    p {
      color: $text-secondary;
      font-size: 18px;
      line-height: 1.6;
      margin-bottom: 2rem;
    }

    .skills_grid {
      display: flex;
      flex-wrap: wrap;
      gap: 15px;
      margin-bottom: 2.5rem;

      @media only screen and (max-width: 968px) {
        justify-content: center;
      }

      .skill_card {
        padding: 10px 20px;
        background: rgba(255, 255, 255, 0.05);
        border: 1px solid rgba(255, 255, 255, 0.1);
        border-radius: 50px;
        font-size: 16px;
        color: $text-primary;
        cursor: default;
        @include transition-ease;
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
      }
    }

    .action-wrapper {
      display: flex;
      @media only screen and (max-width: 968px) {
        justify-content: center;
      }
    }
  }
}
